<template>
  <div class="home-page">
    <h1>Weather Information</h1>
    <p>Select a city to get its weather details:</p>

    <div class="city-buttons">
      
      <button v-for="city in cities" :key="city._id" @click="showWeatherDetails(city)">
        {{ city.city }}
      </button>
    </div>

    <div v-if="selectedCity" class="city-details">
      
      <h2>{{ selectedCity.city }}</h2>
      <p><strong>Temperature:</strong> {{ selectedCity.temperature }}°C</p>
      <p><strong>Humidity:</strong> {{ selectedCity.humidity }}%</p>
      <p><strong>Description:</strong> {{ selectedCity.description }}</p>
      <p><strong>Date:</strong> {{ selectedCity.date }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data() {
    return {
      cities: [],  // List of cities from the backend
      selectedCity: null, // Stores the selected city's data
    };
  },
  methods: {
    fetchWeatherData() {
      fetch('http://localhost:5001/api/weather')
        .then((response) => response.json())
        .then((data) => {
          this.cities = data;
        })
        .catch((error) => console.error('Error fetching weather data:', error));
    },
    showWeatherDetails(city) {
      // Set the selected city data when a button is clicked
      this.selectedCity = city;
    }
  },
  created() {
    // Fetch the weather data when the component is created
    this.fetchWeatherData();
  }
};
</script>

